<template>
  <div>
    <ExampleCard title="Ele Modal ">
      <a-button type="primary" @click="createModal">
        
      </a-button>
      <br />
      <br />
      <a-button type="primary" @click="createCustomTitleModal">
        
      </a-button>
      <br />
      <br />
      <a-button type="primary" @click="createCustomFooterModal">
        footer
      </a-button>
      <br />
      <br />
      <div>
        <div style="text-align:left;">{{ dataText }}</div>
        <a-button type="primary" @click="createDataModal">
          
        </a-button>
      </div>
      <br />
      <a-button type="primary" @click="createDataFromSonModal">
        
      </a-button>
    </ExampleCard>
  </div>
</template>

<script>
import ExampleCard from '@/components/ExampleCard';
import HelloWorld from '@/components/HelloWorld';
import Title from '@/components/Title';
import Footer from '@/components/Footer';
import { Input } from 'ant-design-vue';

export default {
  name: 'EleExample',
  components: {
    ExampleCard,
  },
  data() {
    return {
      dataText: 'aaa',
    };
  },
  methods: {
    createModal() {
      this.$createEleModal({
        modalProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
      });
    },
    createCustomTitleModal() {
      this.$createEleModal({
        modalProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        title: {
          template: Title,
          props: {
            title: '',
          },
        },
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
        beforeClose: function() {
          console.log('');
        },
        afterClose: function() {
          console.log('');
        },
        async onOk() {
          const res = await new Promise((resolve) => {
            setTimeout(() => {
              console.log('');
              resolve(false);
            }, 3000);
          });
          return res;
        },
      });
    },
    createCustomFooterModal() {
      this.$createEleModal({
        modalProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        title: {
          template: Title,
          props: {
            title: '',
          },
        },
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
        footer: {
          template: Footer,
          props: {
            cancelText: '',
            okText: '',
          },
        },
        beforeClose: function() {
          console.log('');
        },
        afterClose: function() {
          console.log('');
        },
        async onOk() {
          await new Promise((resolve) => {
            setTimeout(() => {
              console.log('');
              resolve();
            }, 3000);
          });
        },
      });
    },
    createDataModal() {
      let self = this;
      this.$createEleModal({
        modalProps: {
          title: '',
          okText: '',
          cancelText: '',
          'show-close': false,
        },
        content: {
          template: Input,
          props: {
            value: self.dataText,
          },
          on: {
            change(e) {
              self.dataText = e.target.value;
            },
          },
        },
        onOk() {
          console.log(123123, self.dataText);
        },
      });
    },
    createDataFromSonModal() {
      this.$createEleModal({
        modalProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        // 
        payloadSlot: true,
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
        footer: {
          template: Footer,
          props: {
            cancelText: '',
            okText: '',
          },
        },
        beforeClose: function() {
          console.log('');
        },
        afterClose: function() {
          console.log('');
        },
        onOk(payload) {
          alert('' + JSON.stringify(payload));
        },
      });
    },
  },
};
</script>

<style lang="less" scoped></style>
