<template>
  <div>
    <ExampleCard title="Ele Drawer ">
      <a-button type="primary" @click="createDrawer">
        
      </a-button>
      <br />
      <br />
      <a-button type="primary" @click="createCustomTitleDrawer">
        
      </a-button>
      <br />
      <br />
      <div>
        <div style="text-align:left;">{{ dataText }}</div>
        <a-button type="primary" @click="createDataDrawer">
          
        </a-button>
      </div>
      <br />
      <a-button type="primary" @click="createDataFromSonDrawer">
        
      </a-button>
    </ExampleCard>
  </div>
</template>

<script>
import ExampleCard from '@/components/ExampleCard';
import HelloWorld from '@/components/HelloWorld';
import Title from '@/components/Title';
import { Input } from 'ant-design-vue';

export default {
  name: 'EleDrawerExample',
  components: {
    ExampleCard,
  },
  data() {
    return {
      dataText: 'aaa',
    };
  },
  methods: {
    createDrawer() {
      this.$createEleDrawer({
        drawerProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
        beforeClose: function() {
          console.log('');
        },
        afterClose: function() {
          console.log('');
        },
      });
    },
    createCustomTitleDrawer() {
      this.$createEleDrawer({
        drawerProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        title: {
          template: Title,
          props: {
            title: '',
          },
        },
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
        beforeClose: function() {
          console.log('');
        },
        afterClose: function() {
          console.log('');
        },
      });
    },
    createDataDrawer() {
      let self = this;
      this.$createEleDrawer({
        drawerProps: {
          title: '',
          'show-close': false,
        },
        content: {
          template: Input,
          props: {
            value: self.dataText,
          },
          on: {
            change(e) {
              self.dataText = e.target.value;
            },
          },
        },
        beforeClose: function() {
          console.log('');
        },
        afterClose: function() {
          console.log('');
        },
      });
    },
    createDataFromSonDrawer() {
      this.$createEleDrawer({
        drawerProps: {
          title: 'abcs',
          width: '500px',
          'show-close': false,
        },
        // 
        payloadSlot: true,
        content: {
          template: HelloWorld,
          props: {
            msg: 'Welcome to Your Vue.js App',
          },
        },
        beforeClose: function(payload) {
          alert('beforeClose, ' + JSON.stringify(payload));
          console.log('');
        },
        afterClose: function(payload) {
          alert('afterClose, ' + JSON.stringify(payload));
          console.log('');
        },
      });
    },
  },
};
</script>

<style lang="less" scoped></style>
